<template>
  <div class="addvip-toast fade" v-show="showAddvip">
    <div class="addvip-toast-wrap">
      <div class="addvip-toast-content">
        <div class="addvip-type-top">
          <img class="add-img" src="./images/vip-top.png" alt="">
        </div>
        <div class="addvip-type-list">
          <div v-for="(item,index) in vipTypeList" :key="item.id" class="addvip-item" :class="[index == activeIndex ? 'addvip-item-active' : '']" @click="changeActiveIndex(index,item.id)">
            <div class="top-label"><span>{{ avg(item.discount) }}</span><span>折</span></div>
            <div class="center-text">
              <div class="center-text-title">{{ item.vipTypeName }}</div>
              <div class="center-text-sp">现价:￥{{ item.sellingPrice }}</div>
              <div class="center-text-op"><s>原价:￥{{ item.oldPrice }}</s></div>
            </div>
            <div class="bottom-checked" v-show="index == activeIndex ? true : false"></div>
          </div>
        </div>
        <div class="addvip-title-list">
          <div v-for="(item2,index) in vipTypeList" :key="item2.id" :class="['title-item'+index,'title-item']" v-if="index == activeIndex">
            <div>
              <img src="./images/vip-img.png" alt="">
              每<span v-if="item2.monthAmount == 1">月</span>
              <span v-else-if="item2.monthAmount == 3">季</span>
              <span v-else="item2.monthAmount == 12">年</span>帮你省
              <span class="save-money" v-if="item2.monthAmount == 1">{{ item2.saveMoneyEveryMonth }}</span>
              <span class="save-money" v-else-if="item2.monthAmount == 3">{{ item2.saveMoneyEveryMonth }}</span>
              <span class="save-money" v-else="item2.monthAmount == 12">{{ item2.saveMoneyEveryMonth }}</span>元
            </div>
          </div>
        </div>
        <div class="subAddVip">
          <button type="button" @click="subAddVip">还不是会员?立即开通</button>
        </div>
      </div>
      <div class="addvip-close" @click="hideToastBox">
        <i class="iconfont">&#xe63f;</i>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      vipTypeList: {
        type: Array,
        default:[]
      },
      showAddvip: {
        type: Boolean,
        default: false
      },
      vipTypeDefaultId: {
        type: Number,
        default: 1
      }
    },
    mounted(){

    },
    watch: {
      vipTypeDefaultId(newVal){
        this.checkId = this.vipTypeDefaultId
      }
    },
    data () {
      return {
        isShowToast: false,
        activeIndex: 0,
        checkId: 0
      };
    },
    methods: {
      hideToastBox: function () {
        this.$emit("closeVipBox", false)
      },
      avg(a){
        let res= parseInt((a)*10)/10
        return res
      },
      changeActiveIndex(index,id){
        this.activeIndex = index
        this.checkId = id
      },
      subAddVip(){
        this.$emit("addvip",this.checkId)
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
.addvip-toast
  position fixed
  left 0
  top 0
  bottom 0
  right 0
  background rgba(0,0,0,0.5)
  z-index 999
  .addvip-toast-wrap
    position absolute
    top 50%
    left 50%
    transform translate(-50%,-50%)
    width 19.875rem
    height 36.736rem
    .addvip-toast-content
      background-color #fff
      border-radius 10px
      display inline-block
      position relative
      text-align center
      background rgb(31,28,29)
      height 33.736rem
      width 19.875rem
      .addvip-type-top
        width 100%
        height 16.6875rem
        img
          width 100%
          height 100%
      .addvip-type-list
        margin-top 1.625rem
        padding 0 1.125rem
        display flex
        justify-content space-between
        .addvip-item
          height 6.875rem
          box-sizing border-box
          width 5.7rem
          border 1px solid rgb(49,49,49)
          border-radius 0.4rem
          position relative
          .top-label
            position absolute
            width 1.25rem
            height 2.375rem
            top 0
            right 0.3125rem
            background url("./images/label.png") no-repeat center
            background-size 100% 100%
            font-size 0
            span
              font-size 0.6875rem
              display inline-block
              width 100%
              text-align center
              color #fff
              margin-top 0.2rem
          .center-text
            width 100%
            height 100%
            text-align center
            box-sizing border-box
            padding-top 1.125rem
            .center-text-title
              color rgb(246,205,139)
              font-size 1rem
              font-weight 600
              width 100%
            .center-text-sp
              color rgb(246,205,139)
              font-size 0.8rem
              width 100%
              margin-top 0.875rem
            .center-text-op
              color rgb(114,114,114)
              font-size 0.6875rem
              width 100%
              margin-top 1rem
          .bottom-checked
            position absolute
            width 1.625rem
            height 1.625rem
            bottom -1px
            right 0rem
            background url("./images/vip-check.png") no-repeat center
            background-size 100% 100%
            font-size 0


        .addvip-item-active
          border 1px solid rgb(246,205,139)
          border-radius 0.4rem
      .addvip-title-list
        margin-top 0.625rem
        padding 0 1.125rem
        position relative
        .title-item
          color rgb(246,205,139)
          font-size 0.75rem
          background rgb(49,47,48)
          padding 0.4375rem 0.75rem
          width fit-content
          border-radius 1rem
          text-align left
           position relative
          img
            height 0.7rem
            margin-right 0.4rem
          .save-money
            font-size 0.98rem
        .title-item0
          position absolute
          left 0.75rem
          &::before
            content ''
            position absolute
            height 0.5rem
            border-top 0.5rem solid transparent
            border-left  0.5rem solid transparent
            border-right 0.5rem solid transparent
            border-bottom 0.5rem solid rgb(49,47,48)
            left 2.5rem
            top -1.35rem
        .title-item1
          position absolute
          left 50%
          transform translateX(-50%)
          &::before
            content ''
            position absolute
            height 0.5rem
            border-top 0.5rem solid transparent
            border-left  0.5rem solid transparent
            border-right 0.5rem solid transparent
            border-bottom 0.5rem solid rgb(49,47,48)
            left 50%
            top -1.35rem
            transform translateX(-50%)
        .title-item2
          position absolute
          right 0.75rem
          &::before
            content ''
            position absolute
            height 0.5rem
            border-top 0.5rem solid transparent
            border-left  0.5rem solid transparent
            border-right 0.5rem solid transparent
            border-bottom 0.5rem solid rgb(49,47,48)
            right 2.5rem
            top -1.35rem
            transform translateX(-50%)

      .subAddVip
        position absolute
        bottom 2.125rem
        left 0
        text-align center
        width 100%
        button
          background transparent url("./images/vip-btn.png") no-repeat center
          background-size 100% 100%
          width 11.9375rem
          height 2.5625rem
          border none
          outline none
          font-size 1rem
          color rgb(92,62,9)
    .addvip-close
      width 100%
      height 1.5rem
      line-height 1.5rem
      text-align center
      margin-top 0.6125rem
      i
        height 1.5rem
        width 1.5rem
        color rgb(188,188,188)
        font-size 1.4rem

.fade  {
  -webkit-animation: fade 0.8s;
          animation: fade 0.8s;
}

@-webkit-keyframes fade {
  from {
    opacity 0
  }
  to {
    opacity 1
  }
}

@keyframes fade {
  from {
    opacity 0
  }
  to {
    opacity 1
  }
}
</style>




