import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'



//获取服务中心数据
export function getServiceCenter() {
  return axios.post(CONFIG.API.GETSERVICECENTER, qs.stringify({
    pageKey:  'servce_center'
  })).then(res => res.data)
}

//获取服务中心详情数据
export function getServiceCenterDetail(opts) {
  return axios.post(CONFIG.API.GETSERVICECENTER, qs.stringify({
    pageKey :  opts.pageKey
  })).then(res => res.data)
}

//判断用户是否为vip (包含vip套餐信息)
export function isVip() {
  return axios.post(CONFIG.API.ISVIP).then(res => res.data)
}

//加入vip
export function addVip(opts) {
  return axios.post(CONFIG.API.ADDVIP, qs.stringify({
    vipTypeId :  opts.vipTypeId,
    returnUrl: opts.returnUrl
  })).then(res => res.data)
}


/*
* 购买卡券,立即支付
* */
export function buyCoupon(opts) {
  let data = {}
  if(opts.account){
    data = {
      sku: opts.sku,
      count: 1,
      account: opts.account
    }
  } else {
    data = {
      sku: opts.sku,
      count: 1
    }
  }
  return axios.post(CONFIG.API.BUYCOUPONS, qs.stringify(data)).then(res => res.data)
}




